}
.form__input:focus + .form__input-icon {
  color: rgba(254, 64, 30, 0.3019607843);
}
.form__input:not(:placeholder-shown):invalid + .form__input-icon {
  color: #ff009d;
}
@media (min-width: 768px) {
  .form__input-icon {
    right: 17px;
  }
}

.form__textarea {
  margin-top: 4px;
  padding-left: 15px;
  font: inherit;
  color: inherit;
  background-color: inherit;
  text-transform: initial;
  border: 1px solid #fe401e;
}
.form__textarea:hover {
  border-color: rgba(254, 64, 30, 0.6);
}
.form__textarea:focus {
  outline: 2px solid rgba(254, 64, 30, 0.3019607843);
  outline-offset: -2px;
}
.form__textarea:disabled {
  opacity: 0.3;
}
.form__textarea::placeholder {
  opacity: 1;
}
@media (min-width: 1200px) {
  .form__textarea {
    width: 100%;
    padding: 0 19px;
  }
}

.form__note {
  display: block;
  margin: 0 0 0 10px !important;
  font-size: 9px;
  line-height: normal;
}
@media (min-width: 768px) {
  .form__note {
    margin-left: auto !important;
  }
}
@media (min-width: 1200px) {
  .form__note {
    font-size: 12px;
  }
}

.form__message {
  display: block;
  font-size: 12px;
}
.form__message--success {
  flex-grow: 1;
  color: #297700;
  text-transform: uppercase;
}
.form__message--error {
  flex-grow: 1;
  width: 100%;
  min-width: 280px;
  padding: 20px 0;
  color: #ff009d;
  text-transform: uppercase;
  animation-duration: 2s;
  animation-name: formSlideIn;
}
@media (min-width: 768px) {
  .form__message {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  .form__message {
    font-size: 16px;
  }
}

@keyframes formSlideIn {
  from {
    margin-left: 100%;
    width: 300%;
  }
  to {
    margin-left: 0;
    width: 100%;
  }
}
.form__link {
  margin-left: auto;
  font: inherit;
  color: inherit;
  text-shadow: 2px 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.form__link:hover {
  color: rgba(254, 64, 30, 0.6);
}
.form__link:active {
  color: rgba(254, 64, 30, 0.3803921569);
  text-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.5);
}

.page {
  min-width: 320px;
  height: 100%;
}

.page__body {
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 14px;
    line-height: 14px;
  }
}
@media (min-width: 1200px) {
  .page__body {
    font-size: 16px;
    line-height: 16px;
  }
}

.page__header {
  padding: 20px 0;
  background-color: #f2f2f2;
}

.page__main {
  padding-bottom: 20px;
}

.page__footer {
  padding: 10px 0;
}
@media (min-width: 768px) {
  .page__footer {
    padding: 20px 0;
  }
}
@media (min-width: 1200px) {
  .page__footer {
    padding: 25px 0;
  }
}

.page__body {
  display: grid;
  grid-template-rows: 1fr min-content;
}

.navigation {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
  }
}

.navigation__wrapper {
  display: flex;
  margin-bottom: 20px;
}

.navigation__logo {
  margin: 0;
}

.navigation__button {
  margin-left: auto;
}

.navigation__menu {
  width: 100%;
  margin: 0;
  padding: 0;
}
@media (min-width: 1200px) {
  .navigation__menu {
    width: auto;
    margin-left: auto;
  }
}

.logo,
.custom-logo-link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo__image,
.custom-logo {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 24px;
  cursor: pointer;
}
.burger--nojs {
  display: none;
}
@media (min-width: 1200px) {
  .burger {
    display: none;
  }
}
.burger:hover .burger__icon:not(.burger__icon--opened) {
  background-color: rgba(254, 64, 30, 0.6);
}
.burger:hover .burger__icon::before {
  background-color: rgba(254, 64, 30, 0.6);
}
.burger:hover .burger__icon::after {
  background-color: rgba(254, 64, 30, 0.6);
}
.burger:focus:not(:active) .burger__icon:not(.burger__icon--opened) {
  background-color: #000000;
}
.burger:focus:not(:active) .burger__icon::before {
  background-color: #000000;
}
.burger:focus:not(:active) .burger__icon::after {
  background-color: #000000;
}
.burger:active .burger__icon {
  opacity: 0.3;
}

.burger__icon {
  position: relative;
  display: block;
  width: 50px;
  height: 3px;
  background-color: #000000;
  transform: rotateZ(0);
  transition: 0.4s ease all;
}
.burger__icon::before, .burger__icon::after {
  position: absolute;
  left: 0;
  right: 0;
  height: 3px;
  background-color: #000000;
  content: "";
  transform: rotateZ(0);
  transition: 0.4s ease all;
}
.burger__icon::before {
  top: -10px;
}
.burger__icon::after {
  bottom: -10px;
}
.burger__icon--opened::before {
  top: 0;
  left: 12px;
  width: 24px;
  transform: rotateZ(405deg);
}
.burger__icon--opened::after {
  top: 0;
  left: 12px;
  width: 24px;
  transform: rotateZ(315deg);
}
.burger__icon--opened {
  background-color: transparent;
}

.menu {
  display: none;
  color: #000000;
  background-color: inherit;
  list-style-type: none;
}
.menu--nojs {
  display: flex;
  flex-direction: column;
}
.menu--opened {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .menu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

@media (min-width: 1200px) {
  .menu__item:not(:last-of-type) {
    margin-right: 20px;
  }
}

.menu__link {
  display: block;
  padding: 10px 0;
}
@media (min-width: 1200px) {
  .menu__link {
    position: relative;
    padding: 5px 0;
  }
  .menu__link--current::after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #000000;
    content: "";
  }
}

.index {
  display: flex;
  flex-direction: column;
}

.index__header {
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-template-rows: min-content min-content;
}
@media (min-width: 768px) {
  .index__header {
    grid-template-columns: 150px 1fr;
  }
}
@media (min-width: 768px) {
  .index__header {
    grid-template-columns: 180px 1fr;
  }
}

.index__brand {
  grid-column: 2/3;
  grid-row: 1/2;
