  }
}
@media (min-width: 1200px) {
  .index__brand {
    font-size: 40px;
  }
}

.index__title {
  grid-column: 2/3;
  grid-row: 2/3;
  font-size: 18px;
  line-height: normal;
}
@media (min-width: 768px) {
  .index__title {
    font-size: 24px;
  }
}
@media (min-width: 1200px) {
  .index__title {
    font-size: 32px;
  }
}

.custom-logo-link {
  grid-column: 1/2;
  grid-row: 1/3;
}

.index {
  margin: auto;
  max-width: 480px;
  overflow: hidden;
}

.page-footer {
  display: flex;
  align-items: center;
  justify-content: center;
}